.filterBlock{
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
    margin: 10px auto;
}
.filterContainer{
    width: 90%;
    margin-top: 15px;
    border-radius: 10px;
    border: 3px solid rgba(249, 249, 249, 0.1);
}
.clearFilter{
    color: rgba(255, 255, 255, 0.5);
    text-decoration: underline;
    cursor: pointer;
}
@media screen and (max-width: 1150px){
.filterBlock{
    flex-direction: column;
    align-items: center;
    gap:10px;
}
}